
<template>
  <div class="row">
    <GridVue class="input-field " tamanho="12">
        <GridVue class="input-field " tamanho="12">
          <textarea v-model="conteudo" id="conteudoID" class="materialize-textarea"></textarea>
          <label for="conteudoID">O que está acontecendo?</label>
        </GridVue>                          
            <div v-if="conteudo" class="btn waves-effect waves-light" >
              Publicar
            </div>          
      </GridVue>       
  </div>
</template>    
<script>
import GridVue from "@/components/layout/GridVue";
export default {
  name: "PublicarConteudoVue",
  props: [],
  data() {
    return {
      conteudo:''
    };
  },
  components: {
    GridVue,
  },
};
</script>

<style scoped>
</style>



