<template>  
<SiteTemplate>

  <span slot="menuesquerdo">
    <div class="row valign-wrapper">
        <GridVue tamanho="4"> 
            <img src="https://materializecss.com/images/yuna.jpg" alt="" class="circle responsive-img"> <!-- notice the "circle" class -->
        </GridVue>
        <GridVue tamanho="8"> 
            <span class="black-text">
            <h5>Maria</h5>
            This is a square image.
            </span>
        </GridVue>
      </div>      
  </span>
  <span slot="principal">
    <PublicarConteudoVue/>
    <CardConteudoVue perfil="https://materializecss.com/images/yuna.jpg"
      nome="Maria Silva" data="13/01/2020">
      <CardDetalheVue 
        img="https://st4.depositphotos.com/3169507/24900/v/1600/depositphotos_249000758-stock-illustration-isometric-composition-man-study-designer.jpg" 
        titulo="Maria"
        txt="Little" />
      </CardConteudoVue>  
  </span>

</SiteTemplate>


</template>

<script>
import SiteTemplate from '@/templates/SiteTemplate';
import CardConteudoVue from '@/components/social/CardConteudoVue';
import CardDetalheVue from '@/components/social/CardDetalheVue';
import PublicarConteudoVue from '@/components/social/PublicarConteudoVue';
import GridVue from '@/components/layout/GridVue';
export default {
  name: 'Home',
  data () {
    return {
    }
  },
  components:{
    SiteTemplate,
    CardConteudoVue,
    CardDetalheVue,
    PublicarConteudoVue,
      GridVue 
  }
}
</script>
<style scoped>

</style>



