<template>
  <div id="app">
    <header>
      <NavBar cor="green darken-1" logo="Social" url="#/">
              <li><a href="#/">Home</a></li>
              <li><a href="#/login">Entrar</a></li>    
              <li><a href="#/cadastro">Cadastre-se</a></li>         
      </NavBar>

    </header>
    <main>
      <div class="container">    
        <div class="row">  
            <GridVue tamanho="4"> 
              <CardMenuVue> 
                <slot name="menuesquerdo"/>
              </CardMenuVue>   
              <CardMenuVue>   
                teste
                </CardMenuVue>                       
            </GridVue>       
            <GridVue tamanho="8">
              <slot name="principal"/>
            </GridVue>                          
        </div>         
      </div>      
    </main>
    <FooterVue cor="green darken-1" logo="Social" descricao="Teste de descricao" ano="2020">
      <li><a class="grey-text text-lighten-3" href="#!">Home</a></li>
      <li><a class="grey-text text-lighten-3" href="#!">Link 2</a></li>
      <li><a class="grey-text text-lighten-3" href="#!">Link 3</a></li>
      <li><a class="grey-text text-lighten-3" href="#!">Link 4</a></li>      
    </FooterVue>                  
</div>         
</template>

<script>
import NavBar from '@/components/layout/NavBar';
import FooterVue from '@/components/layout/FooterVue';
import GridVue from '@/components/layout/GridVue';
import CardMenuVue from '@/components/layout/CardMenuVue';
export default {
  name: 'SiteTemplate',
  components:{
    NavBar,
    FooterVue,
    GridVue,
    CardMenuVue
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



